<template>
  <div>

    <div class="column isCentered">
    <router-link to="/">
        <img src="../assets/resto.png" width="40px" height="40px">
        <span id="delicious">DELICIOUS</span>
    </router-link>
    </div>
    <div class="columns has-text-centered ">
      <div class="column is-12">Connectez-vous et accédez à votre compte.</div>
    </div>

    <section>
      <form @submit.prevent="validateBeforeSubmit" autocomplete="off">

        <b-field label="Email">
          <p class="control has-icon has-icon-right">
            <input
              name="email"
              v-model="email"
              v-validate="'required|email'"
              :class="{'input': true, 'is-danger': errors.has('email') }"
              type="text"
              placeholder="Email"
            >
            <i v-show="errors.has('email')" class="fa fa-warning"></i>
            <span v-show="errors.has('email')" class="help is-danger">Vous devez entrez un email valide</span>
          </p>
        </b-field>

        <b-field label="Mot de passe">
          <p class="control has-icon has-icon-right">
            <input
              name="password"
              v-model="password"
              v-validate="'required|min:8'"
              :class="{'input': true, 'is-danger': errors.has('password') }"
              type="password"
              placeholder="Mot de passe"
            >
            <i v-show="errors.has('password')" class="fa fa-warning"></i>
            <span
              v-show="errors.has('password')"
              class="help is-danger"
            >Le mot de passe doit faire 8 caractères minimum</span>
          </p>
        </b-field>

        <button type="submit" class="button is-primary">Se connecter</button>

      </form>
    </section>

  </div>
</template>

<script>
export default {
  data() {
    return {
      email: null,
      password: null
    };
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then(result => {
        if (result) {
          this.$toast.open({
            message: "La connection est en cours !",
            type: "is-success",
            position: "is-bottom"
          });
          console.log(this.email, this.password);
          return;
        }
        this.$toast.open({
          message: "Le formulaire n'est pas valide !",
          type: "is-danger",
          position: "is-bottom"
        });
      });
    }
  }
};
</script>

